

<template>
   <el-card shadow='hover'>
        <div class="createdUser">
            <el-row style="margin-bottom:30px">
                <el-col :span="4">
                    我创建的用户
                </el-col>
                <el-col :span="20" style="text-align:right">
                    <el-button  type='primary' @click="dialogVisible = true">创建新用户</el-button>
                </el-col>
            </el-row>
            <tip txt="暂无用户" oder="3" v-if="isShow"></tip>
            <el-row style="margin:30px 0" v-for="(item, index) in userList" :key="index">
                <el-col :span="3" class="zimu">
                    <span @click="change(index)" :style="{fontSize:(index===active?28:20)+'px',cursor:'pointer'}">{{item.group}}</span>
                </el-col>
                <el-col :span="21">
                    <el-row :gutter="15">
                        <el-col :span="8" v-for="(item, index) in item.users" :key="index">
                            <div class="flexBox">
                                <avatar :name='item.user_abbr' :headIcon = 'item.user_headicon' :bgColor='item.user_backcolor'  size='small' />
                                <div class="flexItem">
                                    <div class="ellipsis">{{item.user_name}}<span style="margin-left:10px">{{item.account}}</span></div>
                                    <div class="ellipsis">{{item.ptime|parseTime('{yyyy}-{mm}-{dd}')}}</div>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                
                </el-col>
            </el-row>
        </div> 
        <el-dialog
        title="创建新用户"
        :visible.sync="dialogVisible"
        >
        <el-form ref="form" :model="form" :rules="rules" label-width="100px">
            <el-form-item label="用户名" prop="account">
                <el-input v-model="form.account" @blur="checkAccount" 
                placeholder="用户名长度大于3位小于30位，支持中文，英文字母，下划线" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="user_pass">
                <el-input type="password" placeholder="密码长度大于5位小于30位" 
                show-password v-model="form.user_pass" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="头像背景色" prop="user_backcolor">
                <span class="user_bg" :style="{background:item}" @click="addUserBgColor(index)"
                 v-for="(item, index) in user_bg_list" :key="index">
                    <i class="el-icon-check" v-if="form.user_backcolor==user_bg_list[index]" 
                    style="color:#fff;position:relative;left:8px;top:-2px"></i>
                 </span>
            </el-form-item>
             <div class="item">
                 
            </div>
        </el-form>
        <div slot="footer" class="dialog-footer" style="text-align:center">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" :loading="loading" @click="confirm">确 定</el-button>
        </div>
        </el-dialog>
    </el-card>
</template>
<script>
import Avatar from '@/components/Avatar'
import { md5Encryption } from "@/utils";
import Tip from '@/components/Tip/index'
export default {
    name: 'CreatedUser',
    components: {
        Avatar,
        Tip
    },
    data() {
      return {
        dialogVisible: false,  
        userList:[],
        active:'',
        user_bg_list: ['#FF7765','#FFB800','#2FC9AE','#169BD5','#556D84','#8400FF'],
        form: {
         account: '',
         user_pass: '',
         user_backcolor:'#FF7765'
        },
        isShow:false,
        loading:false,
        rules: {
          account: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 4,max:29, message: '用户名长度大于3位小于30位', trigger: 'blur' }
          ],
          user_pass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 5,max:29, message: '密码长度大于5位小于30位', trigger: 'blur' }
          ],
        }
      }
    },
    mounted(){
        this.createdbyme()
    },
    methods: {
      change(index){
        this.active = index
      },  
      addUserBgColor(index){
        this.form.user_backcolor = this.user_bg_list[index]
      },
      checkAccount(){
          const { account } = this.form
          if(account!=='' && account.length>3 && account.length<30){
              var regexp = /^[\u4e00-\u9fa5_a-zA-Z]*$/
              const flag = regexp.test(account)
              if(flag){
                this.$store.dispatch('user/checkAccount',{account})
              }else{
                     this.$message({
                            message: '用户名只支持中文，英文字母，下划线',
                            type: 'error'
                        });
              }
           
          }
      },
     createdbyme(){
         this.$store.dispatch('user/createdbyme').then((result)=>{
             this.userList = result.data;
             this.isShow = result.data.length<1?true:false
         })
      },
      cancel(){
          this.dialogVisible = false
          this.form.account = ''
          this.form.user_pass = ''
      },
      confirm() {
          this.$refs.form.validate(valid => {
          if (valid) {
              this.loading = true
              const {account,user_pass,user_backcolor} = this.form;
              const data = {account,user_backcolor,user_pass:md5Encryption(user_pass)}
              this.$store.dispatch('user/adduser',data).then(()=>{
                this.createdbyme()  
                this.$refs.form.resetFields()
                this.dialogVisible = false
                this.loading = false
                this.$message({
                      message: '创建新用户成功！',
                      type: 'success'
                  });
              }).catch(()=>{
                   this.loading = false
              })
          } else {
            console.log('error submit!!')
            return false
          }
        })
          
      }
    }
}
</script>
<style lang="scss" scoped>
@import "~@/styles/variables.scss";
.createdUser {
    padding:10px 20px;
    min-height: 400px;
}
.zimu {
    font-size: 20px;
    margin-top:15px;
}
.flexBox {
    align-items: center;
    margin-bottom: 20px;
}

.flexItem {
    margin-left: 10px;
}

.ellipsis {
    padding:3px 0;
    color: $secondaryText
}
.fontSize {
    font-size: 24px;
    font-weight: bold;
}

.user_bg {
    display: inline-block;
    width: 34px;
    height: 34px;
    margin-right: 20px;
    vertical-align: middle;
    cursor: pointer;
}
</style>


